y: flex;\r\n    align-items: center;\r\n    justify-content: space-between;\r\n    padding: $desktopHeaderPadding;\r\n\r\n    .boxOne {\r\n        @include flexOne;\r\n\r\n        ul.list {\r\n            display: flex;\r\n\r\n            li {\r\n                padding: 0 1rem;\r\n                list-style: none;\r\n\r\n                a {\r\n                    text-decoration: none;\r\n                    color: $grayishViolet;\r\n                    text-transform: capitalize;\r\n                }\r\n\r\n                a:hover {\r\n                    @include hover;\r\n                }\r\n            }\r\n        }\r\n    }\r\n\r\n    .boxTwo {\r\n        @include flexOne;\r\n\r\n        button.login {\r\n            @include button;\r\n            background-color: transparent;\r\n            color: $grayishViolet;\r\n            padding: 0 2rem;\r\n        }\r\n\r\n        button.login:hover {\r\n            @include hover;\r\n        }\r\n\r\n        button.signUp {\r\n            @include button\r\n        }\r\n    }\r\n}\r\n\r\nmain {\r\n\r\n    section.one {\r\n        height: 80vh;\r\n        display: flex;\r\n        align-items: center;\r\n        justify-content: flex-start;\r\n        padding: $desktopHeaderPadding;\r\n\r\n        .contentsContainer {\r\n            width: 35rem;\r\n            transform: translateY(-5rem);\r\n\r\n            h1 {\r\n                @include primaryHeading;\r\n            }\r\n\r\n            p {\r\n                @include paragraph;\r\n            }\r\n\r\n            button.getStarted {\r\n                @include button;\r\n                margin: 2rem 0 0 0;\r\n            }\r\n        }\r\n\r\n        img.illustration {\r\n            position: absolute;\r\n            top:10rem;\r\n            left: 60vw;\r\n        }\r\n    \r\n    }\r\n\r\n    section.two {\r\n        @include flexOne;\r\n        flex-direction: column;\r\n        background-color: $veryGray;\r\n        padding: $desktopHeaderPadding;\r\n\r\n        .inputContainer {\r\n            @include inputBackdrop;\r\n            transform: translateY(-7rem);\r\n\r\n            .inputContents {\r\n                display: grid;\r\n                grid-template-columns: 80% 20%;\r\n                grid-template-rows: 1fr;\r\n                margin: 2rem;\r\n                width: 100%;\r\n\r\n                input.textBox {\r\n                    @include inputTextbox;\r\n                }\r\n\r\n                input.textBox.active::placeholder {\r\n                    color: red;\r\n                }\r\n\r\n                input.textBox.active {\r\n                    @include inputTextboxActive;\r\n                }\r\n    \r\n                button.shortenIt {\r\n                    @include buttonTwo;\r\n                }\r\n\r\n                .warning {\r\n                    visibility: hidden;\r\n\r\n                    p {\r\n                        margin: 0;\r\n                        padding: 0;\r\n                        transform: translateY(1rem);\r\n                        color: $red;\r\n                        text-transform: capitalize;\r\n                        font-style: italic;\r\n                    }\r\n                }\r\n\r\n                .warning.active {\r\n                    visibility: visible;\r\n                }\r\n\r\n            }\r\n        }\r\n\r\n        .inputResultContainer {\r\n            width: 100%;\r\n            transform: translateY(-7rem);\r\n\r\n            ul.inputUl {\r\n                @include inputWrapper;\r\n\r\n                li.inputLi {\r\n                    @include flexOne;\r\n                    justify-content: space-between;\r\n                    text-decoration: none;\r\n                    list-style: none;\r\n\r\n                    .inputContents {\r\n                        @include flexOne;\r\n\r\n                        .result {\r\n                            color: $cyan;\r\n                        }\r\n\r\n                        button.btnResult {\r\n                            @include buttonTwo;\r\n                            margin: 0 0 0 1rem;\r\n                        }\r\n\r\n                        button.btnResult.active {\r\n                            @include buttonThree;\r\n                        }\r\n                    }\r\n                }\r\n            }\r\n        }\r\n    }\r\n\r\n    section.three {\r\n        @include flexOne;\r\n        flex-direction: column;\r\n        padding: 8rem;\r\n        background-color: $veryGray;\r\n        height: 75vh;\r\n\r\n        .subheading {\r\n            @include flexOne;\r\n            flex-direction: column;\r\n            text-align: center;\r\n            align-items: center;\r\n            width: 30rem;\r\n            margin: 0 0 15rem 0;\r\n\r\n            h2 {\r\n                @include secondaryHeading;\r\n                margin: 0 0 2rem 0;\r\n            }\r\n\r\n            p {\r\n                @include paragraphTwo;\r\n                font-weight: 500;\r\n            }\r\n        }\r\n\r\n        .boxes {\r\n            display: grid;\r\n            grid-template-columns: 1fr 1fr 1fr;\r\n            grid-template-rows: 1fr;\r\n            grid-gap: 2%;\r\n            position: relative;\r\n            z-index: 1;\r\n            margin: 0 0 10rem 0;\r\n\r\n            .brandRecognition {\r\n                @include parentDiv;\r\n                transform: translateY(-9rem);\r\n\r\n                .iconOneBackCircle {\r\n                    @include iconBackground;\r\n                }\r\n\r\n                h3 {\r\n                    @include iconHeader;\r\n                }\r\n\r\n                p {\r\n                    @include iconParagraph;\r\n                }\r\n            }\r\n\r\n            .detailedRecords {\r\n                @include parentDiv;\r\n                transform: translateY(-6rem);\r\n\r\n                .iconTwoBackCircle {\r\n                    @include iconBackground;\r\n                }\r\n\r\n                h3 {\r\n                    @include iconHeader;\r\n                }\r\n\r\n                p {\r\n                    @include iconParagraph;\r\n                }\r\n            }\r\n            \r\n            .fullyCustomizable {\r\n                @include parentDiv;\r\n                transform: translateY(-3rem);\r\n\r\n                .iconThreeBackCircle {\r\n                    @include iconBackground;\r\n                }\r\n\r\n                h3 {\r\n                    @include iconHeader;\r\n                }\r\n\r\n                p {\r\n                    @include iconParagraph;\r\n                }\r\n            }\r\n        }\r\n    }\r\n\r\n    section.three::after {\r\n        content: '';\r\n        position: absolute;\r\n        width: calc(100vw - 16rem);\r\n        height: 10px;\r\n        background-color: $cyan;\r\n        z-index: 0;\r\n    }\r\n\r\n    section.four {\r\n        @include flexOne;\r\n        flex-direction: column;\r\n        width: 100vw;\r\n        height: 15rem;\r\n        background-image: url('/src/assets/bg-boost-desktop.svg');\r\n        background-color: $darkViolet;\r\n        background-size: cover;\r\n        background-repeat: no-repeat;\r\n\r\n        h2 {\r\n            @include secondaryHeading;\r\n            color: $white;\r\n            margin: 0 0 2rem 0;\r\n        }\r\n\r\n        button.getStarted {\r\n            @include button;\r\n        }\r\n    }\r\n}\r\n\r\nfooter {\r\n    display: flex;\r\n    align-items: flex-start;\r\n    justify-content: space-between;\r\n    flex-direction: row;\r\n    background-color: $black;\r\n    height: 15rem;\r\n    padding: $desktopSidePadding;\r\n    padding-top: 3rem;\r\n\r\n    img.footerLogo {\r\n        transform: translateY(0.5rem);\r\n    }\r\n\r\n    .footerContents {\r\n        display: flex;\r\n        align-items: flex-start;\r\n        flex-direction: row;\r\n        justify-content: center;\r\n        position: relat